i {
    cursor: pointer;
}
.container {
    min-width: 320px;
    width: 100%;
    padding: 10px;
    overflow: auto;
}
.main-input {
    width: 100%;
    height: 3em;
    padding: 0 10px;
}
.inner-input {
    width: calc( 100% - 110px);
    border: 0;
    outline: 0;
    background: #f4f4f4;
    margin-left: 40px;
    padding-left: 20px;
}
.filter-box {
    display: -ms-flexbox;
    display: flex;
}
.filter-box span {
    -ms-flex: 1 1;
        flex: 1 1;
    border: 5px solid #eee;
    cursor: pointer;
    text-align: center;
}
.list li {
    width: 100%;
    height: 50px;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: justify;
        justify-content: space-between;
    font-size: 20px;
    text-align: center;
    padding: 5px;
    border: 1px solid #eee;
    background: #fff;
}
.content {
    -ms-flex: 1 1;
        flex: 1 1;
    overflow: hidden;
}
.list del {
    margin: 0 auto;
}



